<template>
  <div class="container mt-sm-5 mt-3">

    <div class="row pb-lg-5" v-for="(f, i) in foodObject" :key="i">
      <div class="col-lg-3">
        <div class="position-sticky pb-lg-5 pb-3 mt-lg-0 mt-5 ps-2" style="top: 100px;">
          <h3 class="fs-2 fw-bold text-lg-start">{{ f.title }}</h3>
          <h6 class="text-secondary text-lg-start pe-3">
            {{ f.explantion }}
          </h6>
        </div>
      </div>
      <div class="col-lg-9">
        <div class="row">
          <div class="col-md-4 mt-md-0" v-for="(food, ii) in f.foods" :key="ii">
            <div class="d-flex flex-column">
              <div class="card min-height-160 mt-4">
                <img :src="food.image" alt="..." class="w-100 object-fit-cover rounded" height="160"
                  style="cursor: pointer;" @click="f.toggler = !f.toggler">
              </div>
              <div class="mt-2 ms-2 text-start">
                <h6 class="mb-0 fw-bold">{{ food.name }}</h6>
                <p class="text-secondary">
                  <span class="badge text-bg-danger me-2" v-show="food.best">BEST</span>
                  <span class="badge text-bg-primary me-2" v-show="food.suggestion">추천</span>
                </p>
              </div>
            </div>
          </div>
        </div>
        <FsLightbox :toggler="f.toggler" :sources="f.source" />
      </div>
    </div>

  </div>
</template>
<script>
import FsLightbox from "fslightbox-vue/v3";

export default {
  name: '_Food',
  components: { FsLightbox },
  methods: {
  },
  data() {
    return {
      foodObject: {
        korea: {
          toggler: false,
          title: '한국 음식',
          explantion: '한국 음식 (한식)',
          foods: [
            { image: require('../images/koreanFood/bulgogi.jpg'), name: '불고기', best: true, suggestion: true },
            { image: require('../images/koreanFood/bibimbap.jpg'), name: '비빔밥', best: false, suggestion: true },
            { image: require('../images/koreanFood/samgyeobsal.jpg'), name: '삼겹살', best: false, suggestion: false },
            { image: require('../images/koreanFood/galbi.jpg'), name: '갈비', best: true, suggestion: false },
          ],
          source: [],
        },
        china: {
          toggler: false,
          title: '중국 음식',
          explantion: '중국 음식 (중식)',
          foods: [
            { image: require('../images/chineseFood/jajangmyeon.jpg'), name: '짜장면', best: true, suggestion: true },
            { image: require('../images/chineseFood/tangsuyug.jpg'), name: '탕수육', best: true, suggestion: false },
            { image: require('../images/chineseFood/malatang.jpg'), name: '마라탕', best: false, suggestion: true },
          ],
          source: [],
        },
        japan: {
          toggler: false,
          title: '일본 음식',
          explantion: '일본 음식 (일식)',
          foods: [
            { image: require('../images/japaneseFood/sushi.jpg'), name: '초밥(스시)', best: true, suggestion: true },
            { image: require('../images/japaneseFood/ramen.jpg'), name: '라멘', best: true, suggestion: true },
            { image: require('../images/japaneseFood/takoyaki.jpg'), name: '타코야키', best: true, suggestion: true },
          ],
          source: [],
        },
      },

    }
  },
  mounted() {
    for (let property in this.foodObject) {
      this.foodObject[property].source = this.foodObject[property].foods.map(x => x.image)
    }
  },
}
</script>

<style>
.min-height-160 {
  min-height: 160px !important;
}

.max-height-160 {
  max-height: 160px !important;
}
</style>